import * as THREE from "three";
import { TextureLoader } from "three";
import { debugParams } from "../debug";
import { scene } from "../script";
let particleGeometry = null;
let particleMaterial = null;
let galaxy = null;
const textures= new THREE.TextureLoader();
const starsTexture=textures.load('./Textures/5.png')

export const galaxyGenerator = () => {
  const colors = new Float32Array(debugParams.count * 3);
  const particles = new Float32Array(debugParams.count * 3);
  const colorInside = new THREE.Color(debugParams.insideColor);
  const colorOutside = new THREE.Color(debugParams.outsideColor);
  if (galaxy !== null) {
    particleGeometry.dispose(),
      particleMaterial.dispose(),
      scene.remove(galaxy);
  }

  particleGeometry = new THREE.BufferGeometry();
  particleGeometry.setAttribute("position", new THREE.BufferAttribute(particles, 3));
  particleGeometry.setAttribute('color',new THREE.BufferAttribute(colors,3));

  particleMaterial = new THREE.PointsMaterial({
    size: debugParams.size,
    depthWrite:false,
    blending:THREE.AdditiveBlending,
    vertexColors:true,
    map:starsTexture
  });

  galaxy = new THREE.Points(particleGeometry, particleMaterial);

  for (let i = 0; i < debugParams.count * 3; i++) {
    const i3 = i * 3;
    const branchAngle =
      (Math.PI * 2 * (i % debugParams.branches)) / debugParams.branches;
    /*
    Ricaviamo l'ampiezza degli angoli che dividono le branch moltiplicando PI per il modulo dell'iteratore diviso il numero di branch
    ad esempio:
    i = (0 % 3) / 3 = 0 -> La prima branch avr coordinate Math.cos(0) = 1, Math.sin(0)= 0,( Quindi X:0, Y:1) * Numero casuale fino al limite del raggio
    i = (1 % 3) / 3 = 0.33 -> La seconda branch avr coordinate Math.cos(0.33)=0.94, Math.sin(0.33)=0.32, quindi( X:0.94, Y:0.32 )* Numero casuale fino al limite del raggio
    i = (2 / 3) / 3 = 0.66 -> La terza branch avr coordinate Math.cos(0.66)=0.78, Math.sin (0.66)=0.61. quindi ( X:0.78,Y:0.61)* Numero casuale fino al limite del raggio
    */
    const radius = Math.random() * debugParams.radius;
    const spinAngle = radius * debugParams.spinAngle;
    /*
    Ci serve un modo per curvare i particellari che sono pi lontani dallo 0, sapendo che la posizione dei particellari 
     data dalla branch moltiplicata per il raggio ed il raggio  random con il limite deciso dal client, abbiamo che 
     pi il raggio  vicino allo 0, pi il numero sar piccolo per tanto lo spin Angle pu essere un parametro in moltiplicazione del branch che va a spostare
   in base x ed in base y addizionandosi con il branch 
    */

    const randomX =
      Math.pow(Math.random(), debugParams.randomnessPower) *
      (Math.random() < 0.5 ? 1 : -1);
    const randomY =
      Math.pow(Math.random(), debugParams.randomnessPower) *
      (Math.random() < 0.5 ? 1 : -1);
    const randomZ =
      Math.pow(Math.random(), debugParams.randomnessPower) *
      (Math.random() < 0.5 ? 1 : -1);
    // Aumentiamo il fattore di casualit nella posizione,
    particles[i3] = Math.cos(branchAngle + spinAngle) * radius + randomX; // X
    particles[i3 + 1] = randomY; // Y
    particles[i3 + 2] = Math.sin(branchAngle + spinAngle) * radius + randomZ; // Z

    const mixedColor = colorInside.clone();
    mixedColor.lerp(colorOutside, radius / debugParams.radius);
    colors[i3] = mixedColor.r;
    colors[i3 + 1] = mixedColor.g;
    colors[i3 + 2] = mixedColor.b;
  }

  scene.add(galaxy);
};

export { galaxy };
